.timer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* width: 50%; */
  margin: auto;
  padding: 20px;
  /* border-radius: 8px; */
  /* border: 1px solid var(--secondaryColor); */
  background-color: var(--secondaryContrastColor);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2);
}

.timer > *:not(:last-child) {
  margin-bottom: 20px;
}

.timer__top {
  display: grid;
  grid-template-columns: min-content 1fr;
  justify-items: center;
  align-items: center;
  gap: 15px;
}

.timer__title {
  font-size: 22px;
  text-transform: capitalize;
}

.timer__buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.timer__buttons > button {
  margin: 8px;
  font-weight: bold;
  appearance: none;
  outline: none;
  border: none;
  border-radius: 5px;
  min-width: 120px;
  background-color: var(--secondaryColor);
  color: var(--whiteColor);
  font-size: 16px;
  line-height: 1;
  padding: 10px 15px;
  transition: background-color 0.3s linear;
}
.timer__buttons > button:active,
.timer__buttons > button:focus,
.timer__buttons > button:hover {
  cursor: pointer;
  background-color: var(--primaryContrastColor);
}

.timer .avatar {
  width: 80px;
  height: 23px;
  padding: 0 0 calc(55px * 1 / 1) 0;
}

.timer .avatar > img:focus,
.timer .avatar > img:hover {
  cursor: default;
  border: 2px solid var(--secondaryColor);
}

@media only screen and (max-width: 768px) {
  .timer {
    padding: 20px 0;
    width: 100%;
  }
}

@media only screen and (max-width: 460px) {
  .timer__buttons {
    flex-direction: column;
  }
}
.avatar__img {
  -webkist-animation: spin 4s linear infinite;
  -moz-animation: spin 4s linear infinite;
  animation: spin 4s linear infinite;
}
@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
