.App {
  text-align: center;
}
.timerGrid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  padding: 30px 0;
}
.centerContent {
  display: flex;
  justify-content: center;
  align-content: center;
  padding: 20px;
  margin: auto;
}
.centerContent > img {
  max-width: 100%;
}
.App__container {
  display: grid;
  grid-template-columns: 25fr 50fr 25fr;
  grid-template-areas: "imgOne main imgTwo";
  height: 88vh;
}
.App__container__inner {
  grid-area: main;
}
.image__one__container {
  grid-area: imgOne;
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  z-index: -1;
  margin-right: auto;
}
.image__one {
  opacity: 0.3;
  margin-top: 2rem;
  width: 700px;
}
.image__two {
  grid-area: imgTwo;
  display: flex;
  justify-self: flex-end;
  align-self: flex-end;
}
@media only screen and (max-width: 700px) {
  .App__container {
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    margin-bottom: 5rem;
  }
  .image__one__container {
    position: unset;
  }
  .image__one {
    width: 200px;
  }
}
